// swup
.transition-fade {
  transition: 0.4s;
  opacity: 1;
}

html.is-animating .transition-fade {
  opacity: 0;
}

.no-js .blog-articles {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 24px;
  @media screen and (min-width: 600px) {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  @media screen and (min-width: 960px) {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}

.js .blog-articles {
  padding: 0;
  max-width: 1304px;
  article {
    position: absolute;
    @media screen and (min-width: 522px) {
      img {
        max-width: 350px;
      }
    }
  }
}

.blog_grid {
  margin-bottom: 40px;
  article {
    picture {
      display: block;
      overflow: hidden;
    }
    img {
      display: block;
      transition: var(--transition-slow);
    }
    .title {
      position: relative;
      margin-top: 8px;
      .article_title {
        font-family: var(--font-heading-family);
        font-size: 112.5%;
        position: relative;
        z-index: 1;
      }
      .article_excerpt {
        font-size: 87.5%;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: white;
        z-index: 3;
        opacity: 0;
        transition: var(--transition-slow);
      }
      &.with_excerpt {
        position: relative;
      }
    }
    &:hover {
      .article_excerpt {
        opacity: 1;
      }
      img {
        transform: scale(104%);
      }
    }
  }
}

.image_banner.visually-hidden + .text {
  margin-top: 200px;
}

.article_tags {
  border-bottom: 1px solid var(--color-foreground);
  margin-bottom: 24px;
  a {
    padding: 8px;
    flex: 1 1 auto;
    text-align: center;
    transition: var(--transition);
    &.active {
      font-weight: bold;
    }
  }
}

.inner.intros {
  max-width: 520px;
  text-align: center;
  margin-bottom: 40px;
  margin-top: 40px;
  @media screen and (min-width: 768px) {
    margin-bottom: 80px;
    margin-top: 88px;
  }
}

.blog_intro.visually-hidden + .article_tags {
  margin-top: 300px;
}

